@charset "UTF-8";
/* mixin:功能类 */
*,
*:after,
*:before {
  box-sizing: border-box;
}
html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
  font-family: "PingFangSC-Light", "Helvetica Neue", Helvetica, STHeiTi, sans-serif;
  font-size: 14px;
  line-height: 1;
  background: #f9f9f9;
}
/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
p,
pre {
  padding: 0;
  margin: 0;
  font-weight: 400;
}
/* Links
   ========================================================================== */
a {
  color: #353535;
  text-decoration: none;
}
a:active,
a:hover {
  outline-width: 0;
  cursor: pointer;
}
/* Font adjust
   ========================================================================== */
div,
span,
h1,
h2,
h3,
h4,
h5,
h6,
header,
footer,
section,
header {
  font-size: 100%;
}
/* List
   ========================================================================== */
ol,
ul {
  list-style: none;
}
/* Forms
   ========================================================================== */
::-webkit-input-placeholder {
  color: #999;
}
:-moz-placeholder {
  color: #999;
  opacity: 1;
}
::-moz-placeholder {
  color: #999;
  opacity: 1;
}
:-ms-input-placeholder {
  color: #999;
}
select::-ms-expand {
  display: none;
}
/**
 * 1. Change the font styles in all browsers (opinionated).
 * 2. Remove the margin in Firefox and Safari.
 */
textarea {
  resize: none;
}
input,
textarea,
button,
select {
  outline: none;
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}
button,
input,
optgroup,
select,
textarea {
  font-family: sans-serif;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1.15;
  /* 1 */
  margin: 0;
  /* 2 */
}
/**
 * Show the overflow in IE.
 * 1. Show the overflow in Edge.
 */
button,
input {
  /* 1 */
  overflow: visible;
}
/**
 * Remove the inheritance of text transform in Edge, Firefox, and IE.
 * 1. Remove the inheritance of text transform in Firefox.
 */
button,
select {
  /* 1 */
  text-transform: none;
}
/**
 * 1. Prevent a WebKit bug where (2) destroys native `audio` and `video`
 *    controls in Android 4.
 * 2. Correct the inability to style clickable types in iOS and Safari.
 */
button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
  /* 2 */
}
/**
 * Remove the inner border and padding in Firefox.
 */
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
/**
 * Restore the focus styles unset by the previous rule.
 */
button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
/**
 * Change the border, margin, and padding in all browsers (opinionated).
 */
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
/**
 * 1. Correct the text wrapping in Edge and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 * 3. Remove the padding so developers are not caught out when they zero out
 *    `fieldset` elements in all browsers.
 */
legend {
  box-sizing: border-box;
  /* 1 */
  color: inherit;
  /* 2 */
  display: table;
  /* 1 */
  max-width: 100%;
  /* 1 */
  padding: 0;
  /* 3 */
  white-space: normal;
  /* 1 */
}
/**
 * 1. Add the correct display in IE 9-.
 * 2. Add the correct vertical alignment in Chrome, Firefox, and Opera.
 */
progress {
  display: inline-block;
  /* 1 */
  vertical-align: baseline;
  /* 2 */
}
/**
 * Remove the default vertical scrollbar in IE.
 */
textarea {
  overflow: auto;
}
/**
 * 1. Add the correct box sizing in IE 10-.
 * 2. Remove the padding in IE 10-.
 */
[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
}
/**
 * Correct the cursor style of increment and decrement buttons in Chrome.
 */
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
/**
 * 1. Correct the odd appearance in Chrome and Safari.
 * 2. Correct the outline style in Safari.
 */
[type="search"] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}
/**
 * Remove the inner padding and cancel buttons in Chrome and Safari on OS X.
 */
[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
/**
 * 1. Correct the inability to style clickable types in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  -webkit-appearance: button;
  /* 1 */
  font: inherit;
  /* 2 */
}
/* HTML5 display definitions
   ========================================================================== */
article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
/* Text-level semantics
   ========================================================================== */
em {
  font-style: normal;
}
img {
  border: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
.hidden {
  display: none;
}
body {
  background: #3e4396;
}
.head {
  height: 100px;
  background: #00adff;
  text-align: center;
  padding-left: 1.6rem;
  padding-right: 1.06666667rem;
}
.head-inner {
  position: relative;
  height: 100%;
}
.head-inner img,
.head-inner .title {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.head-inner img {
  left: 0;
  width: 5.44rem;
  padding-right: 0.53333333rem;
  border-right: 4px solid #fff;
}
.head-inner .title {
  right: 0;
  width: 9.97333333rem;
  height: 1.76rem;
  line-height: 1.76rem;
  border-radius: 0.16rem;
  background: #fff;
  color: #00adff;
  font-size: 50px;
  font-size: 1.33333333rem;
}
.content {
  position: relative;
  padding-top: 0.53333333rem;
}
.content .cal-robot {
  width: 5.41333333rem;
  margin-left: 1.46666667rem;
}
.content .active-pic-815 {
  position: absolute;
  width: 9.97333333rem;
  top: 1.46666667rem;
  right: 2.77333333rem;
}
.content .cal-info-list {
  margin-left: 2.05333333rem;
  margin-right: 2.13333333rem;
  padding: 1.14666667rem 0.58666667rem 0.58666667rem 1.06666667rem;
  background: #c2c2c2;
  border: 5px solid #231815;
}
.line {
  margin-bottom: 0.26666667rem;
  padding: 0rem 0.37333333rem;
  border: 6px solid #231815;
  height: 2.29333333rem;
  border-radius: 0.10666667rem;
  background: #fff;
  font-size: 1.04rem;
}
.box {
  position: relative;
  padding-right: 0.64rem;
  height: 100%;
  display: block;
}
.box select {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: transparent;
}
.box::after {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  content: '';
  display: block;
  width: 0;
  height: 0;
  -moz-transform: scale(0.9999);
  border-left-width: 0.32rem;
  border-left-style: solid;
  border-left-color: transparent;
  border-right-width: 0.32rem;
  border-right-style: solid;
  border-right-color: transparent;
  border-top-width: 0.32rem;
  border-top-style: solid;
  border-top-color: #00adff;
}
.box-short {
  width: 48%;
  float: left;
}
.btn {
  background: #00adff;
  line-height: 1.86666667rem;
  text-align: center;
  color: #fff;
}
.line-3 .box-short,
.line-6 .box-short {
  height: 80%;
  top: 50%;
  transform: translateY(-50%);
}
.line-3 .box-short:nth-child(1),
.line-6 .box-short:nth-child(1) {
  margin-right: 0.48rem;
}
.line-3 .box-short:nth-child(2),
.line-6 .box-short:nth-child(2) {
  padding-left: 0.37333333rem;
  border-left-width: 0.10666667rem;
  border-left-style: solid;
  border-left-color: #231815;
}
.line-4 {
  padding-top: 0.32rem;
}
.line-4 span,
.line-4 label {
  display: inline-block;
}
.line-4 label {
  position: relative;
}
.line-4 label:before {
  display: inline-block;
  content: '';
  width: 0.96rem;
  height: 0.96rem;
  border-radius: 0.96rem;
  background: #3e4e96;
}
.line-4 input[type="radio"]:checked + label:after {
  content: '';
  position: absolute;
  top: 23%;
  left: 10%;
  width: 0.48rem;
  height: 0.48rem;
  border-radius: 0.48rem;
  background: #00adff;
}
.line-5,
.line-7 {
  display: block;
  height: 100%;
  line-height: 1.65333333rem;
}
.line-8 {
  display: block;
}
